.recipe-viewer{
    .info{
        background-color: var(--accent);
        padding: 10px;
        box-sizing: border-box;
        gap: 20px;
        display: flex;
        flex-direction: column;
    }
    h1{
        font-family: 'Quartzo';
        font-size:80px;
        max-width:80%;  
    }
    h2{
        font-family: 'General Sans';
        font-size: 25px;
        font-weight: 600;
        
    }
    h3{
        font-family: 'General Sans';
        font-size:20px;
        font-weight:500;
    }
    .blurb{
        display: flex;
        flex-direction: column;
        gap: 5px;
        max-width: 80%;
        h3{
            margin-left:5px;
        }
    }
    .ingredients{
        margin-bottom: 30px;
        .container{
            padding: 5px;
            display: flex;
            gap: 15px;
            .col{
                display: flex;
                flex-direction: column;
                width: 50%;
                gap: 10px;
                .ingredient{
                    font-size:18px;
                }
            }
        }
        
    }
    .instructions{
        padding: 10px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        gap: 20px;
        h1{
            font-size:35px;
            margin:5px 0 0 5px;
            font-family: 'General Sans';
        }
        .container{
            padding: 5px;
            display: flex;
            flex-direction: column;
            gap: 15px;
            .instruction{
                transition: all 0.3s;
                // no blue box on tap
                -webkit-tap-highlight-color: transparent;

                cursor:pointer;
                box-shadow:var(--shadow);
                background-color: white;
                font-size:18px;
                display: flex;
                flex-direction: column;
                border-radius: 15px;
                .text{
                    display: flex;
                    flex-direction: column;
                    gap: 5px;
                    padding: 10px;
                    svg{
                        font-size:20px;
                        transition: all 0.3s;
                        height:20px;
                        width:20px;
                        border-radius: 50%;
                    }
                    b{
                        position: relative; 
                        &::after{
                            content: '';
                            position: absolute;
                            width:0;
                            left:-20px;
                            height:2px;
                            top: 50%;
                            transition: all 0.1s ease-out, background-color 0.3s;
                            border-radius: 2px;
                        }
                    }
                    .step{
                        font-size: 20px;
                        font-family: 'General Sans';
                        font-weight: 500;
                        display: flex;
                        align-items: center;
                        gap: 5px;
                        -webkit-user-select: none;
                        user-select: none;
                    }
                    p{
                        font-size:20px;
                    }
                }
                &.checked{
                    filter: grayscale(1) brightness(0.9);
                    transform:scale(0.995);
                    box-shadow: var(--light-shadow);
                    .text{   
    
                        svg{
                            color: var(--green);
                            animation: radiate 0.5s forwards;
                        }
                    }
                    b{
                        color: var(--darkborder);
                        animation: bounce 0.5s forwards ease-in-out;
                        &::after{
                            content: '';
                            width:105%;
                            background-color: var(--darkborder);
                            left:-2%;
                            
                        }
                    }
                    p{
                        color: var(--darkborder);
                    }
                    
                    @keyframes bounce {
                        0% {
                            transform: translateX(0);
                        }
                        50% {
                            transform: translateX(5px);
                        }
                        100% {
                            transform: translateX(0);
                        }
                    }
                    @keyframes radiate {
                        // green glow
                        0% {
                            box-shadow: 0 0 0 0 var(--green);
                        }
                        100% {
                            box-shadow: 0 0 0 10px rgba(0, 255, 0, 0);
                        }

                        
                    }
                }
            }
            
        }
    }
    
}

@media (max-width:700px) {
    .recipe-viewer{
        .info{
            h1{
                font-size: 50px;
                max-width: 100%;
            }
            .blurb{
                max-width: 90%;
            }
            h2{
                font-size: 20px;
            }
            h3{
                font-size: 17px;
            }
            
        }
    }
    
}