.tip{
    display: flex;
    background-color: var(--accent);
    border-radius: 10px;
    padding: 3px;
    padding-bottom: 5px;
    box-sizing: border-box;
    gap: 5px;
    margin-top: 5px;
    .content{
        display: flex;
        flex-direction: column;
        gap: 5px;
        h3{
            font-family: 'General Sans Italic';
            font-size: 14px;
            font-weight: 600;
            margin-left: 5px;
        }
        .speech-bubble{
            background-color: var(--background);
            padding: 3px 10px;
            border-radius: 5px;
            font-family: 'General Sans';
            font-weight: 500;
            margin-right: 10px;
            position: relative;
            p.tip-text{
                font-size:14px;
            }
            &:before{
                content: '';
                position: absolute;
                left:-3px;
                top: 10px;
                transform: translateY(-50%) rotate(45deg);
                width:10px;
                height:10px;
                background-color: var(--background);
            }
        }
    }
    img{
        height: 45px;
        width: 45px;
        border: 3px solid var(--accent);
        border-radius: 50%;
        transform: translateY(-10px);
    }
}