:root{
    --shadow: 0px 7px 29px 0px rgba(100, 100, 111, 0.3);
    --border: #DDDCDC;
    --accent: #FCE187;
    --background: white;
    --light-bounding: #F2F2F2;
    --green: #00A86B;
    --darkborder: #757575;
    --shadow: 0px 7px 29px 0px rgba(100, 100, 111, 0.2);
    --light-shadow: 0px 7px 29px 0px rgba(100, 100, 111, 0.1);
}

body{
    margin:0;
}

p, h1, h2, h3, h4, h5, h6{
    margin:0;
}

button{
    cursor:pointer;
    border:none;
    background:none;
    outline:none;
    background-color: white;
    padding:5px 20px;
    border-radius: 5px;
    
}

a{
    text-decoration: none;
    color: black;
}