.home{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #f5f5f5;
    overflow:hidden;
    button{
        margin-right:10px;
        color:white;
        background-color: black;
        font-family: 'General Sans';
        font-weight:500;
    }
    .book{
        height:450px;
        width:300px;
        background-color: var(--accent);
        display: flex;
        flex-direction: column;
        align-items: center;
        border-left: 10px solid black;
        border-radius: 0 10px 10px 0;
        box-shadow: var(--shadow);
        transition: all 1s;
        .title{
            width:60%;
            transition: all 0.5s;
            img{
                width:100%;
                margin-top:20px;
            }
        }
        p{
            font-family: 'General Sans';
            font-size: 20px;
            font-weight: 600;
            margin: 10px 0;
            transition: all 0.5s;
        }
        &.open{
            transform:scale(5);
            // position: absolute;
            .title, p{
                opacity: 0;
            }
        }
    }
    .index{
        height:100%;
        width:100%;
        position: absolute;
        top:0;
        left:0;
        z-index: 99;
        animation: fadeIn 1s forwards;
        animation-delay:0.3s;
        opacity: 0;
        padding: 10px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        header{
            display: flex;
            align-items: center;
            justify-content: center;
            padding:20px;
            box-sizing: border-box;
        }
        .content{
            width:500px;
            .tip-text{
                font-size:16px !important;
            }
            .letter{
                animation: jumping 0.5s infinite;
                position: relative;
                &::after{
                    content: '';
                    position: absolute;
                    width:2px;
                    height:2px;
                    border-radius: 50%;
                    background-color: rgb(221, 46, 68);
                    top:0px;
                    right:0px;
                }
                &.open{
                    animation:none;
                }
            }
        }
        .recipes{
            h1{
                font-family: 'General Sans';
                font-size: 30px;
                font-weight: 600;
                margin: 10px 0;
            }
            .recipe{
                display: flex;
                flex-direction: column;
                gap: 10px;
                padding: 10px;
                border-radius: 10px;
                box-shadow: var(--shadow)
            }
        }
    }
}

@keyframes fadeIn{
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

@keyframes jumping{
    0%{
        transform: translateY(0);
    }
    30%{
        transform: translateY(-10px);
    }
    100%{
        transform: translateY(0);
    }
}