.note{
    background-color: var(--accent);
    padding: 3px;
    box-sizing: border-box;
    display: flex;
    border-radius: 5px;
    align-items: center;
    gap: 10px;
    img{
        max-height:100%;
        height:30px;
        width:30px;
    }
    .speech-bubble{
        background-color: var(--background);
        padding: 3px 10px;
        border-radius: 5px;
        font-family: 'General Sans';
        font-weight: 500;
        font-size: 14px;
        margin-right: 10px;
        position: relative;
        &:before{
            content: '';
            position: absolute;
            left:-5px;
            top: 50%;
            transform: translateY(-50%) rotate(45deg);
            width:10px;
            height:10px;
            background-color: var(--background);
        }
    }
    &.squished{
        flex-direction: column;
        align-items: flex-start;
        .speech-bubble:before{
            left:10px;
            top:0px;
        }
    }
}